  get_matches(_competition, isFirstRequest) {
    this.matches_loaded = false
    this.played_matches = []
    this.incoming_matches = []
    this._AppService.get_data(`teams/matches?id=${this.id}&competitionId=${_competition}&date=${this.selected_date}`, 's').subscribe((res: any) => {
      this.matches_loaded = true
      this.selected_competition = _competition
      if (isFirstRequest) {
        this.data = res.data
        this._competitions = this.data.competitions
        // console.log(this._competitions[0].competition.matches)
      } else {
      }
      for (let i = 0; i < res.data.competitions.length; i++) {
        if (res.data.competitions[i].checked) {
          if(res.data.competitions[i].matches){
            this.incoming_matches = res.data.competitions[i].matches
          }
          if(res.data.competitions[i].played_matches){
            this.played_matches = res.data.competitions[i].played_matches
          }  
          if (this.played_matches.length == 0 && this.incoming_matches.length == 0) {
            this.no_matches = true
            this.is_matches_line = false
          } else {
            this.no_matches = false
            this.is_matches_line = true
          }
          if (this.played_matches.length == 0 || this.incoming_matches.length == 0) {
            this.is_matches_line = false
          }
        }
      }
      let el = this._ElementRef.nativeElement.querySelector('.matches_status_filter')
      setTimeout(() => {
        if(el){
          el.scrollIntoView()
        }
        this.data_loaded = true
      }, 100);
    })
  }

  get_table() {
    this.standing_loaded = false
    this._AppService.get_data(`teams/table?id=${this.id}&competitionId=`, 's').subscribe((res: any) => {
      // console.log(res.data)
      this.standing_loaded = true
      this._tables_competitions = res.data.competitions
      // console.log(this._tables_competitions)
      if (this._tables_competitions.length > 0) {
        this.no_standing = false
        for (let i = 0; i < this._tables_competitions.length; i++) {
          if(this._tables_competitions[i].competition.format == 'domestic_league'){
            if(this._tables_competitions[i].league_table.length > 0){
              this._tables_arr.push({
                'name': this._tables_competitions[i].competition.name,
                'table': this._tables_competitions[i].league_table
              })
            }
          } else {
            if(this._tables_competitions[i].group_table.teams.length > 0){
              this._tables_arr.push({
                'name': this._tables_competitions[i].competition.name,
                'table': this._tables_competitions[i].group_table.teams
              })
            }
          }
          //-- if no tables
          if(this._tables_arr.length == 0 ){
            this.no_standing = true
          }
        }
      } else {
        this.no_standing = true
      }
    })
  }

  get_top_scorers(_competitionId) {
    this.top_scorers_loaded = false
    this._AppService.get_data(`teams/topScorer?id=${this.id}&competitionId=${_competitionId}`, 's').subscribe((res: any) => {
      this.top_scorers_loaded = true
      this._top_scorers = res.data.topScorer
      // console.log(this._top_scorers)
      if (this._top_scorers.length == 0) {
        this.no_top_scorers = true
      } else {
        this.no_top_scorers = false
      }
    })
  }

  get_players() {
    this._AppService.get_data(`teams/squad?id=${this.id}`, 's').subscribe((res: any) => {
      // console.log(res.data.squad)
      this._players = res.data.squad.players
      // console.log(this._players)
      if (this._players)
        this.__GK = this._players.GK
      this.__Def = this._players.Def
      this.__Mid = this._players.Mid
      this.__Att = this._players.Att
      if (this.__GK.length == 0 && this.__Def.length == 0 && this.__Mid.length == 0 && this.__Att.length == 0) {
        this.no_players = true
      }
    })
  }

  tabs(index) {
    this._ElementRef.nativeElement.querySelector('.large-header__tabs li.active').classList.remove('active')
    this._ElementRef.nativeElement.querySelector('.main-tabs-box__tab.active').classList.remove('active')
    this._ElementRef.nativeElement.querySelectorAll('.large-header__tabs li')[index].classList.add('active')
    this._ElementRef.nativeElement.querySelectorAll('.main-tabs-box__tab')[index].classList.add('active')
    this._AppService.scroll_to_top()
  }


  add_favorite(id){
    this._AppService.user_post_data(`user/savespeciallist`,{favourite_team:[id]},'s',localStorage.getItem('token'))
    .subscribe((res:any)=>{
      // console.log(res)
    })
  }

  get_favorites(){
    if(this.if_user_logged){
      this._AppService.user_get_data(`user/favourite`,'s',localStorage.getItem('token'))
      .subscribe((res:any)=>{
        // console.log(res)
        this.favorites_teams = res.data.favorite_teams
        for(let i=0;i<this.favorites_teams.length;i++){
          if(this.favorites_teams[i].teamId == this.id){
            this._ElementRef.nativeElement.querySelector('._fav').checked = true
          }
        }
      })
    }
  }

  months_arabic = ["يناير", "فبراير", "مارس", "إبريل", "مايو", "يونيو",
  "يوليو", "أغسطس", "سبتمبر", "أكتوبر", "نوفمبر", "ديسمبر"];

transform_date(__date) {
  
  __date = __date.replace(/:[^:]*$/, '')
  let _generated_date = __date.split("/")
  let _final_date = `${_generated_date[2]} ${this.months_arabic[parseInt(_generated_date[1]) - 1]} ${_generated_date[0]}`
  return _final_date
}


  _router(url){
    this._AppService.router_navigation(url)
  }


  back() {
    this._AppService.go_back()
    console.log('back')
  }


  ngOnInit() {
    this.if_user_logged = this._UserService.is_user_logged(false)
    this.get_favorites()
    this.id = this._ActivatedRoute.snapshot.params['id'];
    this.get_matches('', true)
    this.get_table()
    this.get_top_scorers('')
    this.get_players()



  }

